/** @jsx jsx **/
import {jsx} from '@emotion/react'
import React from "react";
import {Form, Input} from "antd";

export interface User {
    id: string,
    name: string,
    email: string,
    title: string,
    organization: string,
    token?: string
}

interface SeachPanelProps {
    users: User[],
    param: {
        name: string,
        personId: string
    },
    setParam: (param: SeachPanelProps["param"]) => void
}

export const SearchPanel = (props: SeachPanelProps) => {
    return (
        <Form css={{marginBottom: '2rem'}} layout={"inline"}>
            <Form.Item>
                {/*等价于setParam((Object.assign({},param,{name:evt.target.value})))*/}
                <Input
                    placeholder={'项目名'}
                    type="text"
                    value={props.param.name}
                    onChange={(evt) => {
                        props.setParam({
                            ...props.param,
                            name: evt.target.value,
                        });
                    }}
                />
            </Form.Item>
            <Form.Item>
                <select
                    value={props.param.personId}
                    onChange={(evt) =>
                        props.setParam({
                            ...props.param,
                            personId: evt.target.value,
                        })
                    }
                >
                    <option value={""}>负责人</option>
                    {props.users.map((user) => (
                        <option key={user.id} value={user.id}>
                            {user.name}
                        </option>
                    ))}
                </select>
            </Form.Item>
        </Form>
    )
}
